import React, { useEffect, useState } from 'react'
import Loading from '../components/Loading'
import { useParams, Link } from 'react-router-dom'

const url = 'https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i='


const SingleCocktail = () => {

  let { id } = useParams();
  const [cocktail, setCocktail] = useState({})
  const [loading , setLoading] = useState(false)
  
  useEffect(() => {
    setLoading(true);
    async function fetchSingleCocktail() {
      try {
        let response = await fetch(`${url}${id}`);
        let data = await response.json();
        if (data.drinks) {
          let {
            strDrink: name,
            strCategory: category,
            strAlcoholic: type,
            strGlass: glass,
            strInstructions: instructions,
            strDrinkThumb: image,
            strIngredient1,
            strIngredient2,
            strIngredient3,
            strIngredient4,
          
          } = data.drinks[0];

          let singleCocktail = {
            name, category, type, glass, instructions, image, strIngredient1,
            strIngredient2,
            strIngredient3,
            strIngredient4,
          }

          setCocktail(singleCocktail);
        }
        else {
          setCocktail(null)
        }
        setLoading(false)
      } catch (error) {
        throw new Error(error)
      }
    }
    fetchSingleCocktail()
    
  } , [id])
  if (loading) {
    return <Loading></Loading>
  }
  
  const { name, image, category, type, instructions, glass } = cocktail;
  let {
    strIngredient1,
    strIngredient2,
    strIngredient3,
    strIngredient4,
  } = cocktail;
  return (
    <section className="section cocktail-section">
      <Link to='/' className='btn btn-primary'>Back Home</Link>
      <h2 className='section-title'> {name}  </h2>
      <div className="drink">
        <img src={image} alt={name} />
        <div className="drink-info">
          
          <p>
            <span className='drink-data'>name :</span>
            {name}
          </p>
          <p>
            <span className='drink-data'>category :</span>
            {category}
          </p>
          <p>
            <span className='drink-data'>Info :</span>
            {type}
          </p>
          <p>
            <span className='drink-data'>Glass :</span>
            {glass}
          </p>
          <p>
            <span className='drink-data'>Instructions</span>
            {instructions}
          </p>
          <p>
            <span className='drink-data'>Ingredients :</span>
            <span>{strIngredient1} </span>
            <span> {strIngredient2}</span>
            <span> {strIngredient3} </span>
            <span> {strIngredient4}</span>
          </p>
        </div>
      </div>
    </section>
  )
}

export default SingleCocktail
